<template>
  <div
    :class="{ 'bg-white': !nightMode, 'bg-dark': nightMode }"
    class="pt-5 p-st"
    ref="controler"
  >
    <div
      class="container"
      data-aos="fade"
      data-aos-once="true"
      data-aos-duration="1000"
    >
      <div class="row align-items-center">
        <div class="col-xl-6 col-bg-6 col-md-6 col-sm-12">
          <div id="scene-container" ref="sceneContainer"></div>
        </div>
        <div class="col-xl-6 col-bg-6 col-md-6 col-sm-12 pt-5">
          <span
            class="home-title"
            :class="{ pgray: !nightMode, 'text-light': nightMode }"
            >Hi!
          </span>
          <div>
            <p v-html="description"></p>
          </div>
          <div class="text-center pb-4">
            <button
              class="btn btn-outline-secondary mx-2"
              @click="open('linkedin')"
              v-tooltip.bottom="'LinkedIn'"
            >
              <i class="fab fa-linkedin"></i>
            </button>
            <button
              class="btn btn-outline-secondary mx-2"
              @click="open('github')"
              v-tooltip.bottom="'GitHub'"
            >
              <i class="fab fa-github"></i>
            </button>
            <button
              class="btn btn-outline-secondary mx-2"
              @click="open('resume_slo')"
              v-tooltip.bottom="'Resume SLO'"
            >
              <i class="fa fa-file"></i>
            </button>
            <button
              class="btn btn-outline-secondary mx-2"
              @click="open('resume_eng')"
              v-tooltip.bottom="'Resume ENG'"
            >
              <i class="fa fa-file"></i>
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import info from "../../info";
import * as THREE from "three";
import { OrbitControls } from "three/examples/jsm/controls/OrbitControls";
import { GLTFLoader } from "three/examples/jsm/loaders/GLTFLoader";
//import { MathUtils } from "three";
//import { Interaction } from "three.interaction";

export default {
  name: "Home",
  components: {},
  props: {
    nightMode: {
      type: Boolean,
    },
  },
  data() {
    return {
      container: null,
      canvas: null,
      scene: null,
      camera: null,
      controls: null,
      renderer: null,
      raycaster: null,
      resized: null,
      controler: null,
      obj: null,
      plane: null,
      mouse: null,
      planeAspectRatio: null,
      fov: null,
      pointOfIntersection: null,
      description: info.description,
      resume: info.links.resume,
      linkedin: info.links.linkedin,
      github: info.links.github,
      name: info.name,
    };
  },
  methods: {
    open(link) {
      switch (link) {
        case "linkedin":
          window.open(this.linkedin, "_blank");
          break;
        case "github":
          window.open(this.github, "_blank");
          break;
        case "resume":
          window.open(this.resume, "_blank");
          break;
      }
    },
    mouseMove(event) {
      //this.mouse.x = (event.clientX / window.innerWidth) * 50 - 1;
      //this.mouse.y = -(event.clientY / window.innerHeight) * 50 + 1;
      //this.raycaster.setFromCamera(this.mouse, this.camera);
      //this.raycaster.ray.intersectPlane(this.plane, this.pointOfIntersection);
      //this.obj.lookAt(this.pointOfIntersection);
      //requestAnimationFrame(this.animate);
      //this.plane = new THREE.Plane(new THREE.Vector3(0, 0, 1), -10);
      //this.raycaster = new THREE.Raycaster();
      //this.mouse = new THREE.Vector2();
      //this.pointOfIntersection = new THREE.Vector3();
      if (this.obj) {
        this.targetX = (event.clientX / window.innerWidth) * 2 - 1;
        this.targetY = -(event.clientY / window.innerHeight) * 2 + 1;
        //this.obj.rotation.y += 0.01;
        this.obj.rotation.y = 0.8 * (this.targetX - this.obj.rotation.y);
        this.obj.rotation.x = -0.2 * (this.targetY - this.obj.rotation.x);
        this.obj.rotation.z = 0.1 * (this.targetY - this.obj.rotation.x);
      }
    },
    //mouseEnter() {
    //console.log("mouseenter");
    //this.container.addEventListener("mousemove", this.mouseMove(), false);
    //},
    //mouseLeave() {
    //this.$el.removeEventListener("mousemove", this.mouseMove());
    //},
    init() {
      // set container
      this.container = this.$refs.sceneContainer;
      this.controler = this.$refs.controler;

      this.fov = 1;
      // add camera
      const aspect = this.container.clientWidth / this.container.clientHeight;
      const near = 1; // the near clipping plane
      const far = 1000; // the far clipping plane
      const camera = new THREE.PerspectiveCamera(this.fov, aspect, near, far);
      camera.position.set(0, 0, 25);
      this.camera = camera;

      // create scene
      this.scene = new THREE.Scene();

      // add lights
      const ambientLight = new THREE.HemisphereLight(
        0xffffff, // bright sky color
        0x222222, // dim ground color
        3 // intensity
      );
      const mainLight = new THREE.DirectionalLight(0xffffff, 4.0);
      mainLight.position.set(5, 10, 10);
      this.scene.add(ambientLight, mainLight);

      // const interaction = new Interaction(
      //   this.renderer,
      //   this.scene,
      //   this.camera
      // );

      // add controls
      this.controls = new OrbitControls(this.camera, this.container);
      this.controls.enableZoom = false;
      if (!this.$isMobile()) {
        this.controls.enabled = false;
        this.controler.addEventListener("mousemove", this.mouseMove);
      } else {
        this.controls.enabled = true;
        this.controler.removeEventListener("mousemove", this.mouseMove);
      }

      const loader = new GLTFLoader();

      loader.load("/avatar.glb", (gltf) => {
        this.obj = gltf.scene;
        var box = new THREE.Box3().setFromObject(this.obj);
        var center = new THREE.Vector3();
        box.getCenter(center);
        this.obj.position.sub(center);
        this.scene.add(this.obj);
      });

      // create renderer
      this.renderer = new THREE.WebGLRenderer({
        antialias: true,
        alpha: true,
      });
      this.renderer.setSize(
        this.container.clientWidth,
        this.container.clientHeight
      );
      this.renderer.setPixelRatio(window.devicePixelRatio);
      this.renderer.gammaFactor = 2.2;
      this.renderer.outputEncoding = THREE.sRGBEncoding;
      this.renderer.physicallyCorrectLights = true;
      this.container.appendChild(this.renderer.domElement);

      this.animate();
      window.addEventListener("resize", () => {
        this.camera.aspect =
          this.container.clientWidth / this.container.clientHeight;
        this.camera.updateProjectionMatrix();
        this.renderer.setSize(
          this.container.clientWidth,
          this.container.clientHeight
        );
        this.renderer.setPixelRatio(window.devicePixelRatio);

        var box = new THREE.Box3().setFromObject(this.obj);
        var center = new THREE.Vector3();
        box.getCenter(center);
        this.obj.position.sub(center);
      });
    },
    animate() {
      requestAnimationFrame(this.animate);
      //this.targetX = this.mouseX * 0.001;
      //this.targetY = this.mouseY * 0.001;
      //this.obj.rotation.y += 0.01;
      //this.obj.rotation.y = 0.8 * (this.targetX - this.obj.rotation.y);
      //this.obj.rotation.x = 0.2 * (this.targetY - this.obj.rotation.x);
      //this.obj.rotation.z = 0.1 * (this.targetY - this.obj.rotation.x);
      this.renderer.render(this.scene, this.camera);
    },
  },
  mounted() {
    this.init();
  },
};
</script>

<style scoped>
.home-title {
  font-size: 35px;
  font-weight: 500;
}

/*#scene-container {
  min-height: 400px;
  min-width: 300px;
  border: none;
  outline: none;
}
*/

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  #scene-container {
    margin: 0 auto;
    min-height: 350px;
    max-height: 500px;
    max-width: 400px;
    border: none;
    outline: none;
  }
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
  #scene-container {
    margin: 0 auto;
    max-height: 400px;
    min-height: 300px;
    max-width: 400px;
    border: none;
    outline: none;
  }
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
  #scene-container {
    margin: 0 auto;
    min-height: 300px;
    max-width: 300px;
    border: none;
    outline: none;
  }
}

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
  #scene-container {
    margin: 0 auto;
    min-height: 400px;
    max-width: 300px;
    border: none;
    outline: none;
  }
}

/* Extra large devices (large laptops and desktops, 1200px and up) */
@media only screen and (min-width: 1200px) {
  #scene-container {
    margin: 0 auto;
    min-height: 600px;
    min-width: 600px;
    max-height: 600px;
    border: none;
    outline: none;
  }
}
@media only screen and (min-width: 1800px) {
  #scene-container {
    margin: 0 auto;
    min-height: 600px;
    min-width: 700px;
    max-height: 600px;
    border: none;
    outline: none;
  }
}

.fa {
  font-size: 15px;
}
.btn {
  border-color: #669db3ff;
  color: #669db3ff;
}
.btn:hover {
  background-color: #669db3ff;
  border-color: #669db3ff;
  color: white;
}
.btn:focus {
  background-color: #669db3ff;
  border-color: #669db3ff;
  color: white;
}
.btn:focus {
  outline: none !important;
}
p {
  text-align: justify;
  font-weight: 400;
}
</style>
